<template>
    <div
        class="p-datatable p-component p-datatable-responsive-scroll p-datatable-sm p-mt-1"
        data-scrollselectors=".p-datatable-wrapper"
    >
        <div class="p-datatable-wrapper">
            <table role="table" class="p-datatable-table">
                <thead class="p-datatable-thead" role="rowgroup">
                    <tr role="row">
                        <th style="width: 120px;">
                            <div class="p-column-header-content">
                                <span class="p-column-title">
                                    Cycle
                                </span>
                            </div>
                        </th>
                        <th colspan="4">
                            <div class="p-column-header-content">
                                <span class="p-column-title">
                                    {{ cycleLabel }}
                                </span>
                            </div>
                        </th>
                    </tr>
                </thead>

                <tbody class="p-datatable-tbody" role="rowgroup">
                    <tr class="" role="row">
                        <td style="width: 120px;">
                            First Name
                        </td>
                        <td colspan="4">{{ patient.firstName }}</td>
                    </tr>
                    <tr class="" role="row">
                        <td style="width: 120px;">
                            Last Name
                        </td>
                        <td colspan="4">{{ patient.lastName }}</td>
                    </tr>
                    <tr class="" role="row">
                        <td style="width: 120px;">
                            Birth Date
                        </td>
                        <td>
                            {{ patient.day }}-{{ patient.month }}-{{
                                patient.year
                            }}
                        </td>
                        <td style="width: 80px;">
                            Age
                        </td>
                        <td>
                            {{ patient.age }}
                        </td>
                    </tr>
                    <tr class="" role="row">
                        <td style="width: 120px;">
                            Health Card
                        </td>
                        <td>{{ patient.hin }}</td>
                        <td style="width: 80px;">
                            Gender
                        </td>
                        <td>
                            {{ patient.sex }}
                        </td>
                    </tr>
                    <tr class="" role="row">
                        <td style="width: 120px;">
                            Tel
                        </td>
                        <td colspan="4">{{ patient.phone }}</td>
                    </tr>
                </tbody>
            </table>
        </div>

        <div class="p-column-resizer-helper" style="display: none;"></div>
    </div>
</template>

<script>
import { computed, inject } from "vue";
import { useStore } from "vuex";
export default {
    setup() {
        const store = useStore();
        const patient = computed(() => store.state.artCycle.patient);
        const isCycle = inject("isCycle");

        const cycleLabel = computed(() => {
            if (isCycle.value == 1) {
                return store.state.artCycle.selectedCycle.cycleId;
            } else {
                return "No Cycle";
            }
        });

        return { patient, cycleLabel };
    }
};
</script>
