import { DATE_FORMAT_STRING } from "@/config";
import { emitter } from "@/event-bus";
import { format } from "date-fns";
import { useToast } from "primevue/usetoast";
import { computed, inject, reactive, ref } from "vue";
import { useStore } from "vuex";
import { serialize } from "php-serialize";
import { useConfirm } from "primevue/useconfirm";
import { NL_FORM_BASE } from "@/constants";

export function useBloodTestsReportInit() {
    const store = useStore();
    const orderBloodTestConfirmDialog = inject("orderBloodTestConfirmDialog");
    const editDailogVisible = inject("editDailogVisible");

    const testResults = ref();
    const selectedBloodTest = inject("selectedBloodTest");
    const loading = ref(false);
    const getBloodTestResults = () => {
        loading.value = true;
        store
            .dispatch("bloodTest/getBloodTestResults")
            .then((data) => {
                testResults.value = data;
            })
            .finally(() => (loading.value = false));
    };

    const getBloodTestResultsWithOpenBarcode = (test) => {
        loading.value = true;
        store
            .dispatch("bloodTest/getBloodTestResults")
            .then((data) => {
                testResults.value = data;
            })
            .finally(() => (loading.value = false));
    };

    const barcodeDailogVisible = ref(false);

    const showBarcode = (id) => {
        store.dispatch("bloodTest/printBarcodePDF", id).then((pdfUrl) => {
            let width = 520;
            let height = 580;
            let left = screen.width / 2 - width / 2;
            let top = screen.height / 2 - height / 2;

            window.open(
                pdfUrl,
                "_blank",
                `height=${height},width=${width},top=${top},left=${left}`
            );
        });
    };

    const showBarcodeV2 = (data) => {
        const obj = {
            bar_id: data.testOrderId,
            pCodesLine: data.testItems
        };

        let url = `${NL_FORM_BASE}/jrsPHPclientV2/jrs/generate_reportn1.php?report_name=TESTS&report_type=pdf&report_params=${encodeURIComponent(
            serialize(obj)
        )}`;
        window.open(url);
    };

    const openPdfReport = () => {
        const params = {
            patientId: store.state.artCycle.patient.id,
            patientName: `${store.state.artCycle.patient.firstName} ${store.state.artCycle.patient.lastName}`,
            apiToken: store.state.user.accessToken
        };
        const url = `${NL_FORM_BASE}/cycles-reports/print-blood-test-report.php?${new URLSearchParams(
            params
        )}`;
        window.open(url);
    };

    return {
        store,
        testResults,
        loading,
        getBloodTestResults,
        getBloodTestResultsWithOpenBarcode,
        orderBloodTestConfirmDialog,
        editDailogVisible,
        barcodeDailogVisible,
        selectedBloodTest,
        showBarcode,
        showBarcodeV2,
        openPdfReport
    };
}

export function useBloodTestReportInit() {
    const dialogVisible = inject("dialogVisible");
    return { dialogVisible };
}

export function useBloodTestDailogInit() {
    const store = useStore();
    const toast = useToast();
    const orderBloodTestDialog = inject("orderBloodTestDialog");
    const isCycle = inject("isCycle");

    const form = reactive({
        testOrderId: null,
        physicianId: store.state.artCycle.patient.doctorId,
        date: format(new Date(), DATE_FORMAT_STRING),
        site: null,
        isCycle: null,
        testIdArray: [],
        stat: null,
        serverId: null,
        deviceId: null,
        testPriority: null,
        btComments: null,
        label: null,
        bloodMachine: null
    });
    const resetForm = () => {
        form.physicianId = store.state.artCycle.patient.doctorId;
    };

    const bloodMachineOptions = reactive([1, 2, 3, 4]);

    const submitted = ref(false);
    const isInvalidTests = ref(false);
    const isInvalidLocation = ref(false);

    const showBarcode = (test) => {
        const obj = {
            bar_id: test.barcode,
            pCodesLine: test.testItems
        };

        let url = `${NL_FORM_BASE}/jrsPHPclientV2/jrs/generate_reportn1.php?report_name=TESTS&report_type=pdf&report_params=${encodeURIComponent(
            serialize(obj)
        )}`;
        window.open(url);
    };

    const confirm = useConfirm();
    const changeDoctorConfirmation = () => {
        if (form.physicianId != store.state.artCycle.patient.doctorId) {
            confirm.require({
                message: "Are you sure you want to change doctor?",
                header: "Confirmation",
                icon: "pi pi-exclamation-triangle",
                accept: () => {
                    save();
                },
                reject: () => {
                    resetForm();
                }
            });
        } else {
            save();
        }
    };

    const loading = ref(false);
    const save = () => {
        submitted.value = true;
        if (form.testIdArray.length == 0) {
            isInvalidTests.value = true;
            return;
        } else {
            isInvalidTests.value = false;
        }

        if (form.site == null || form.site === "") {
            isInvalidLocation.value = true;
            return;
        } else {
            isInvalidLocation.value = true;
        }

        loading.value = true;
        const reqBody = {
            ...form,
            ...store.getters["artCycle/getSelectedCycleAndPatientId"],
            testIdArray: [...form.testIdArray],
            isCycle: isCycle.value
        };

        store
            .dispatch("bloodTest/addBloodTestOrderItems", reqBody)
            .then((data) => {
                toast.add({
                    severity: "success",
                    summary: "",
                    detail: "Save Successful",
                    life: 3000
                });

                emitter.emit(
                    "blood-test-edit-dailog:reload-test-results-with-open-barcode",
                    data
                );

                showBarcode(data);
            })
            .finally(() => {
                submitted.value = false;
                orderBloodTestDialog.value = false;
                loading.value = false;
            });
    };

    return {
        store,
        orderBloodTestDialog,
        form,
        resetForm,
        changeDoctorConfirmation,
        save,
        submitted,
        loading,
        isInvalidTests,
        isInvalidLocation,
        bloodMachineOptions
    };
}

export function useBloodTestBarcodeInit() {
    const form = reactive({
        barcode: "yes",
        carrierNumber: 1,
        positionInCarrier: 1,
        system: "rack"
    });

    const disabled = computed(() => form.barcode === "yes");

    return {
        form,
        disabled
    };
}

export function useBloddTestInfoInit() {
    const store = useStore();

    const form = inject("form");
    const isInvalidLocation = inject("isInvalidLocation", false);

    const doctorOptions = computed(() => store.state.doctor.doctors);
    const locationOptions = computed(() => store.state.provider.locations);
    const testPriorityOptions = reactive([
        { value: "R", label: "Routine" },
        { value: "S", label: "Stat" },
        { value: "A", label: "As Soon As Possible" },
        { value: "T", label: "In Time" },
        { value: "P", label: "Preoperative" }
    ]);

    return {
        form,
        doctorOptions,
        locationOptions,
        testPriorityOptions,
        isInvalidLocation
    };
}

export function useBloodTestGroupSelectionInit() {
    const store = useStore();

    const form = inject("form");
    const submitted = inject("submitted");
    const isInvalidTests = inject("isInvalidTests");

    const testGroup = ref([]);
    const testGroupsOptions = computed(
        () => store.getters["bloodTest/getBloodGroups"]
    );
    const testSelectionTable = computed(() => {
        if (testGroup.value.length == 0) return [];
        else if (testGroup.value.includes("all")) {
            const booodTests = [];
            for (const group of store.state.bloodTest.bloodGroups) {
                booodTests.push(...group.testItems);
            }
            return [
                ...new Map(
                    booodTests.map((item) => [item["testId"], item])
                ).values()
            ];
        } else {
            const booodTests = [];
            for (const group of store.state.bloodTest.bloodGroups) {
                if (testGroup.value.includes(group.curGroup)) {
                    booodTests.push(...group.testItems);
                }
            }
            return [
                ...new Map(
                    booodTests.map((item) => [item["testId"], item])
                ).values()
            ];
        }
    });
    const seletedTests = ref([]);

    return {
        form,
        testSelectionTable,
        seletedTests,
        testGroup,
        testGroupsOptions,
        submitted,
        isInvalidTests
    };
}

export function useBloodTestEditDialogInit() {
    const store = useStore();
    const toast = useToast();
    const editDailogVisible = inject("editDailogVisible");
    const selectedBloodTest = inject("selectedBloodTest");
    const isCanceled = ref(false);

    const form = reactive({
        cycleId: null,
        bloodTestOrderDate: null,
        patientId: null,
        testOrderId: null,
        bloodTestResultDate: null,
        etwo: null,
        etwoStat: null,
        lh: null,
        lhStat: null,
        prog: null,
        progStat: null,
        fsh: null,
        fshStat: null,
        tsh: null,
        tshStat: null,
        prl: null,
        prlStat: null,
        bhcg: null,
        bhcgStat: null,
        dheas: null,
        dheasStat: null,
        testo: null,
        testoStat: null,
        amh: null,
        amhStat: null
    });

    const emptyFrom = () => {
        form.cycleId = null;
        form.bloodTestOrderDate = null;
        form.patientId = null;
        form.testOrderId = null;
        form.bloodTestResultDate = null;
        form.etwo = null;
        form.etwoStat = null;
        form.lh = null;
        form.lhStat = null;
        form.prog = null;
        form.progStat = null;
        form.fsh = null;
        form.fshStat = null;
        form.tsh = null;
        form.tshStat = null;
        form.prl = null;
        form.prlStat = null;
        form.bhcg = null;
        form.bhcgStat = null;
        form.dheas = null;
        form.dheasStat = null;
        form.testo = null;
        form.testoStat = null;
        form.amh = null;
        form.amhStat = null;
    };

    const getBloodTest = () => {
        isCanceled.value = false;
        store
            .dispatch(
                "bloodTest/getBloodTest",
                selectedBloodTest.value.testOrderId
            )
            .then((data) => {
                form.cycleId = data.cycleId;
                form.bloodTestOrderDate = data.bloodTestOrderDate;
                form.patientId = data.patientId;
                form.testOrderId = data.testOrderId;
                form.bloodTestResultDate = data.bloodTestResultDate;
                form.etwo = data.etwo;
                form.etwoStat = data.etwoStat;
                form.lh = data.lh;
                form.lhStat = data.lhStat;
                form.prog = data.prog;
                form.progStat = data.progStat;
                form.fsh = data.fsh;
                form.fshStat = data.fshStat;
                form.tsh = data.tsh;
                form.tshStat = data.tshStat;
                form.prl = data.prl;
                form.prlStat = data.prlStat;
                form.bhcg = data.bhcg;
                form.bhcgStat = data.bhcgStat;
                form.dheas = data.dheas;
                form.dheasStat = data.dheasStat;
                form.testo = data.testo;
                form.testoStat = data.testoStat;
                form.amh = data.amh;
                form.amhStat = data.amhStat;
            })
            .catch(() => {
                emptyFrom();
            });
    };

    const saveAll = () => {
        store
            .dispatch("bloodTest/saveAllBloodTest", { ...form })
            .then((data) => {
                toast.add({
                    severity: "success",
                    summary: "Successful",
                    detail: "Save Successful",
                    life: 3000
                });
                editDailogVisible.value = false;
                emitter.emit("blood-test-edit-dailog:reload-test-results");
            })
            .catch(() => {});
    };

    const deleteResult = () => {
        store
            .dispatch(
                "bloodTest/deleteResult",
                selectedBloodTest.value.testOrderId
            )
            .then((data) => {
                toast.add({
                    severity: "success",
                    summary: "Successful",
                    detail: "Save Successful",
                    life: 3000
                });
                editDailogVisible.value = false;
                emitter.emit("blood-test-edit-dailog:reload-test-results");
            })
            .catch(() => {});
    };

    const deleteOrder = () => {
        store
            .dispatch(
                "bloodTest/deleteOrder",
                selectedBloodTest.value.testOrderId
            )
            .then((data) => {
                toast.add({
                    severity: "success",
                    summary: "Successful",
                    detail: "Save Successful",
                    life: 3000
                });
                editDailogVisible.value = false;
                emitter.emit("blood-test-edit-dailog:reload-test-results");
            })
            .catch(() => {});
    };

    const cancelBloodTest = (testCode) => {
        store
            .dispatch("bloodTest/cancelBloodTest", {
                testOrderId: selectedBloodTest.value.testOrderId,
                testCode
            })
            .then((data) => {
                isCanceled.value = true;
                toast.add({
                    severity: "success",
                    summary: "Successful",
                    detail: "Save Successful",
                    life: 3000
                });
            })
            .catch(() => {});
    };

    const onHide = () => {
        if (isCanceled.value) {
            emitter.emit("blood-test-edit-dailog:reload-test-results");
        }
    };

    return {
        editDailogVisible,
        form,
        getBloodTest,
        saveAll,
        deleteOrder,
        deleteResult,
        cancelBloodTest,
        onHide
    };
}
