<template>
    <p-dialog
        header="Order Blood Test"
        v-model:visible="orderBloodTestConfirmDialog"
        :style="{ width: '460px' }"
        :modal="true"
        class="p-inputtext-sm p-input-filled"
    >
        <div class="p-grid">
            <p>This Blood test will not be attced to the cycle!</p>
            <p>
                If you want this blood test to be attched to cycle, please
                create it from cycle monitoring page
            </p>
        </div>

        <p-button label="Ok" class="p-button-success p-mr-2" @click="onOK" />

        <p-button label="Cancel" class="p-button-danger" @click="onCancel" />
    </p-dialog>
</template>

<script>
import { inject } from "vue";
export default {
    setup() {
        const orderBloodTestDialog = inject("orderBloodTestDialog");
        const orderBloodTestConfirmDialog = inject("orderBloodTestConfirmDialog");
        const isCycle = inject("isCycle");

        const onOK = () => {
            isCycle.value = 0;
            orderBloodTestDialog.value = true;
            orderBloodTestConfirmDialog.value = false;
        };

        const onCancel = () => {
            orderBloodTestConfirmDialog.value = false;
        };


        return { orderBloodTestConfirmDialog, onOK, onCancel };
    }
};
</script>
