<template>
    <p-fieldset legend="Test Group" class="p-inputtext-sm p-input-filled">
        <p-listbox
            v-model="testGroup"
            :options="testGroupsOptions"
            optionLabel="groupName"
            optionValue="curGroup"
            :multiple="true"
            listStyle="height: 200px"
        />
    </p-fieldset>

    <p-fieldset
        legend="Test Selectiion"
        class="p-inputtext-sm p-input-filled p-mt-2"
        :class="[submitted && isInvalidTests ? 'invalid' : '']"
    >
        <span v-if="submitted && isInvalidTests" style="color: red">
            Select at least 1
        </span>

        <p-data-table
            :value="testSelectionTable"
            responsiveLayout="scroll"
            showGridlines
            class="p-datatable-sm p-mb-1"
            v-model:selection="seletedTests"
            dataKey="testId"
            :scrollable="true"
            scrollHeight="200px"
        >
            <p-column field="testId" header="Code" style="max-width: 60px" />
            <p-column field="testName" header="Name" />
            <p-column selectionMode="multiple" style="max-width: 3rem" />
        </p-data-table>
    </p-fieldset>
</template>

<script>
import { watch } from "vue";
import { useBloodTestGroupSelectionInit } from "@/composable/blood-tests";
export default {
    setup() {
        const {
            form,
            testGroup,
            testGroupsOptions,
            testSelectionTable,
            seletedTests,
            submitted,
            isInvalidTests
        } = useBloodTestGroupSelectionInit();

        watch(seletedTests, (newVal) => {
            form.testIdArray = newVal.map((item) => ({
                itemId: item.testId,
                name: item.testName
            }));
            isInvalidTests.value = false;
        });

        watch(testSelectionTable, (newVal) => {
            seletedTests.value = newVal;
        });

        return {
            seletedTests,
            testGroup,
            testGroupsOptions,
            testSelectionTable,
            submitted,
            isInvalidTests
        };
    }
};
</script>

<style scoped>
.invalid {
    border: 1px solid red;
}
</style>
